import config from '../config.js'
import store from '../store/index.js'

const url_ = config.baseUrl

function request(res) {

	// url,data = {},istoken = 1
	const that = this
	const url = res.url;
	const data = res.data;
	const method = res.method;
	const istoken = res.istoken == 0 ? res.istoken:1;
	
	return new Promise((resolve, rejectd) => {
		let token, 
		req_hea,
		req_data, 
		full_path = url;
		req_data = { ...data};
		
		
		// token
		if (istoken == 1) {
			// token = uni.getStorageSync('token')//token
			req_data.token = uni.getStorageSync('token')
			// tokenrejectd
			// if (!req_data.token) {
			// 	return rejectd('token')
			// }
		}
		// url
		if (url.indexOf('https') == -1) {
			full_path = url_ + url
		}
		
		// post,
		if (method == 'POST') {
			req_hea = {
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST'
			}
		}
		

		uni.request({
			url: full_path,
			data: req_data,
			...req_hea,
			success(res) {
				if (res.data.code == 200) {
					resolve(res.data)
										
				} else if (res.statusCode == 500 || res.statusCode == 404) {
					rejectd(res.statusCode)
				} else if (res.data.code == 300) {
					console.log('token300')					
					
					uni.showToast({
						title: "",
						icon: "none"
					})
					
				} else {
					rejectd(res.data)
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			},
			fail(err) {
				uni.showToast({
					icon: "none",
					title: ""
				})
				console.log('err', err)
				rejectd(err)
			}
		})
	})
}

export default request
