import request from '../request.js';

//  h5
function getSignature(data){
	return request({
		url:'user/wx/getSignature',
		data,
		istoken:0
	})
}


//  h5
function H5Login(data){
	return request({
		url:`user/wx/officialAccountLogin`,
		istoken:0,
		data
	})
}


// 
function getactivelist(data) {
	return request({
		url:'user/index/index',
		data,
		method:'POST',
		
	})
}
